.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.todo-list {
  position: relative;
}

.todos-table {
  border-collapse: collapse;
}

.todos-table td {
  padding: 10px 15px;
  border: 1px solid #000;
}

.add-todo-form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.add-todo-form__input-container {
  position: relative;
  margin: 20px 0;
}

.add-todo-form__error-message {
  position: absolute;
  top: -100px;
  left: 0;
  padding: 20px;
  max-width: 200px;
  z-index: 10;

  color: #ff0000;
  background-color: #fff;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.add-todo-form__error-message::after {
  content: '';
  position: absolute;
  right: 4px;
  bottom: -10px;
  margin-left: -0.5em;
  box-sizing: border-box;


  width: 0;
  height: 0;
  border: 5px solid #fff;
  border-color: transparent transparent #fff #fff;

  transform-origin: 0 0;
  transform: rotate(-45deg);

  box-shadow: 0 3px 3px 0 rgba(0, 0, 0, 0.1);
}
